export default {
	data() {
		return {};
	},
	methods: {
		goDetail(type) {
			let url = '';
			switch (type) {
				case 'peiyou':
					url = '../peiyou/peiyou'
					break;
				case 'ceping':
					url = '../ceping/ceping'
					break;
				case '书本':
					url = '../book/book'
					break
				default:
					break;
				
			}
			uni.navigateTo({
				url: url,
			});
		}
	}
};
